import { gql } from '@apollo/client';

export const GET_ALL_DISCUSSIONS = gql`
  query GET_ALL_DISCUSSIONS($offset: Int, $sort: DiscussionSortInput) {
    allDiscussions(cursor: { offset: $offset, limit: 10 }, sort: $sort) {
      totalCount
      results {
        id
        isFinished
        endsAt
        backgroundPictureUrl
        title
        description
        responsesCount {
          Total
        }
        tags
        photoAuthor {
          name
          profileUrl
          portfolioUrl
        }
        opinionsCount {
          Total
          Neutral
          AgreeAnd
          AgreeBut
          Disagree
        }
        deepLink
        author {
          fullName
          pictureUrl
        }
      }
    }
  }
`;
