import { useLazyQuery } from '@apollo/client';
import React, { FC, useEffect, useState } from 'react';
import { Discussion } from '../../typings';
import { GET_ALL_DISCUSSIONS } from './apollo/queries/discussions';
import DiscussionCard from './components/DiscussionCard';
import styles from './LiveDiscussionsList.module.scss';
import ReactPaginate from 'react-paginate';
import Skeleton from 'react-loading-skeleton';
import { times } from 'lodash';

type Data = {
  allDiscussions: {
    totalCount: number;
    results: Discussion[];
  };
};

type PaginationParams = {
  selected: number;
};

const LiveDiscussionsList: FC = () => {
  const [totalCount, setTotalCount] = useState(0);
  const [fetchAllDiscussions, { data, called, loading }] = useLazyQuery<Data>(
    GET_ALL_DISCUSSIONS,
    {
      variables: { offset: 0, sort: { order: 'ASC', orderBy: 'time_left' } },
      fetchPolicy: 'cache-and-network',
    },
  );

  useEffect(() => {
    !called &&
      fetchAllDiscussions({
        variables: { offset: 0, sort: { order: 'ASC', orderBy: 'time_left' } },
      });
  }, [called, fetchAllDiscussions]);

  useEffect(() => {
    if (data?.allDiscussions.totalCount) {
      setTotalCount(data?.allDiscussions.totalCount);
    }
  }, [data?.allDiscussions.totalCount]);

  const handlePageClick = ({ selected }: PaginationParams) => {
    const offset = 10 * selected;
    fetchAllDiscussions({
      variables: {
        offset: offset ? offset + 1 : offset,
        sort: { order: 'ASC', orderBy: 'time_left' },
      },
    });
    window.scrollTo(0, 0);
  };

  return (
    <div className={styles.content}>
      <div className={styles.discussions_container}>
        {loading &&
          times(10, () => (
            <div className={styles.skeleton_wrapper}>
              <Skeleton duration={0.5} width={872} height={250} />
            </div>
          ))}
        {data?.allDiscussions.results.map((item, index) => (
          <DiscussionCard key={index} discussion={item} />
        ))}
      </div>

      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        previousClassName={styles.hide_border}
        nextClassName={styles.hide_border}
        breakLabel={'...'}
        containerClassName={styles.pagination}
        previousLinkClassName={styles.pagination__link}
        nextLinkClassName={styles.pagination__link}
        disabledClassName={styles.pagination__link__disabled}
        activeClassName={styles.pagination__link__active}
        initialPage={0}
        pageCount={Math.ceil(totalCount ? totalCount / 10 : 0)}
        marginPagesDisplayed={1}
        pageRangeDisplayed={5}
        onPageChange={handlePageClick}
      />
    </div>
  );
};

export default LiveDiscussionsList;
